import React from 'react';
import ReactDOM from 'react-dom';
import * as utils from './utils.js';

class DeckOfCards extends React.Component {

    constructor() {
        super();
        console.log('my m y', utils.newDeck());
        this.state = {};
    }

    drop(ev) {
        ev.preventDefault();

    }

    allowDrop(ev) {

    }

    onDragStart(ev) {
ev.dataTransfer.setData("text", ev.target.id);
    }

    render() {
        return (
            <div>
                <div id="div1" onDrop={this.drop} onDragOver={this.allowDrop}>
                    <p draggable="true" onDragStart={this.onDragStart} id="drag1" width="88"
                       height="31">Hello</p>
                </div>

                <div id="div2" ondrop="drop(event)" ondragover="allowDrop(event)"></div>
            </div>
        )
    }

}

ReactDOM.render(
    <DeckOfCards/>
    , document.getElementById('app'))


